import './App.css';
import { useState } from 'react';
import "https://kit.fontawesome.com/ce91c21e99.js"
import Login from "./Components/Ligin"
import Sidebar from './Components/Sidebar';
import Feed from './Components/Feed';
import Widgets from './Components/Widgets';
import { createUserWithEmailAndPassword, onAuthStateChanged } from 'firebase/auth';
import { auth } from "./firebase.js"

function App() {
  const [authenticated, setAuthenticated] = useState(false);
  function signUp(e) {
    e.preventDefault();
    createUserWithEmailAndPassword(auth, e.target.email, e.target.password)
    .then(creds => console.log(creds))
    .catch(err => console.log(err))
  }
  onAuthStateChanged(auth, user => {
    console.log(user)
    if (user) {
      setAuthenticated(true);
    }
  });
  
  return (
    <div className="App">
      {/* {!authenticated ? <Login signUpForm={signUp} /> :
        <> */}
          <Sidebar />
          <Feed />
          <Widgets/>
     
    </div>
  );
}

export default App;
